.library {
      position: fixed;
      top: 0;
      left: 0;
      width: 20rem;
      height: 100%;
      box-shadow: 2px 2px 50px rgb(184, 184, 184);
      background: white;
      overflow: scroll;
      transform: translateX(-100%);
      transition: all 0.5s ease;
      opacity: 0;
      h2 {
            padding: 2rem;
      }
}

.library-song {
      display: flex;
      align-items: center;
      padding: 1rem 2rem 1rem 2rem;
      cursor: pointer;
      transition: background 0.5s ease;
      img {
            width: 30%;
      }
      &:hover {
            background: rgb(202, 188, 252);
      }
}

.song-description {
      padding-left: 1rem;
      h3 {
            font-size: 1rem;
      }
      h4 {
            font-size: 0.7rem;
      }
}

* {
      scrollbar-width: thin;
      scrollbar-color: rgba(121, 121, 121, 0.5) transparent;
}

*::-webkit-scrollbar {
      width: 5px;
      height: 100%;
}

*::-webkit-scrollbar-thumb {
      background: #a0a0a0;
      border: transparent;
      -webkit-border-radius: 1ex;
}
*::-webkit-scrollbar-track {
      background: transparent;
}

.selected {
      background: rgb(159, 141, 209);
}

.active-library {
      transform: translateX(0%);
      opacity: 1;
}

@media screen and (max-width: 768px) {
      .library {
            width: 100%;
      }
}
