.player {
      min-height: 20vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
}

.time-control {
      width: 50%;
      display: flex;
      align-items: center;

      input {
            width: 100%;
            -webkit-appearance: none;
            background: transparent;
            cursor: pointer;
      }
      p {
            padding: 1rem;
      }
}

.play-control {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1rem;
      width: 30%;
      svg {
            cursor: pointer;
      }
}

input[type="range"] {
      &:focus {
            outline: none;
      }
      &::-webkit-slider-thumb {
            appearance: none;
            -webkit-appearance: none;
            height: 17px;
            width: 16px;
      }
}

.track {
      background: lightblue;
      width: 100%;
      height: 1.2rem;
      position: relative;
      border-radius: 1rem;
      overflow: hidden;
}

.animate-track {
      background: rgb(204, 204, 204);
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transform: translateX(30%);
      pointer-events: none;
}

@media screen and (max-width: 768px) {
      .time-control {
            width: 90%;
      }
      .play-control {
            width: 80%;
      }
}
